
import React from 'react';
import PropTypes from "prop-types"
import { makeStyles } from '@material-ui/core/styles';

import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';
import Box from '@material-ui/core/Box';


import EmailIcon from '@material-ui/icons/Email';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';

import avatar from "../images/avatar.jpg"

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: "100%",
    height: "calc(100vh - 56px)",
    ["@media (min-width: 0px) and (orientation: landscape)"]: {
      height: "calc(100vh - 48px)",
    },
    [theme.breakpoints.up('sm')]: {
      height: "calc(100vh - 64px)",
    },
  },
  container: {
    flexGrow: 1,
    height: "100%",
  },
  avatar: {
    width: "200px",
    height: "200px",
    objectFit: "cover",
    margin: "0 auto",
    borderRadius: "50%",
  },
  moon: {
    width: "200px",
    height: "200px",
    borderRadius: "100%",
    background: "#ddd",
    margin: "0 auto",
    transition: "1s",
    position: "absolute",
    boxShadow: "0 0 50px rgb(255 215 0 / 0.75), 0 0 150px rgb(255 215 0 / 0.75), inset 0 20px 20px 20px rgb(245 245 245 / 0.5), inset 30px 0 35px 11px rgb(255 255 240 / 0.5)",
    "&:hover": {
      boxShadow: "0 0 0px tomato,0 0 0px orange, inset 0 0px 0px 0px #ddd, inset 0px 0 0px 0px #ccc",
      background: "rgb(0,0,0,0)",
      transition: "1s",
    }
  },
  iconContainer: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
  },
  icon: {
    fontSize: 44,
    color: theme.palette.secondary.main,
    transition: "transform .2s", /* Animation */
    "&:hover": {
      transform: "scale(1.25)",
    },
  }
}));

const Banner = ({ siteTitle }) => {
  const classes = useStyles();
  const preventDefault = (event) => event.preventDefault();

  return (
    <div className={classes.root}>
      <Grid
        container
        direction="row"
        justify="center"
        alignItems="center"
        className={classes.container}
      >
        <Grid
          item
          container
          direction="column"
          justify="center"
          alignItems="center"
        >
          <Grid item>

            <a href="https://henry32144.github.io/hugo-website/">
              <Box className={classes.moon} />
              <img className={classes.avatar} alt="CHENGHAN WU" src={avatar} />
            </a>
          </Grid>
          <Grid
            item
            container
            direction="row"
            justify="center"
            alignItems="center"
            className={classes.iconContainer}
            spacing={1}
          >
            <Grid item>
              <a href="mailto:henry3214456@gmail.com">
                <EmailIcon className={classes.icon} />
              </a>
            </Grid>
            <Grid item>
              <a href="https://www.linkedin.com/in/cheng-han-wu-0803/?locale=en_US">
                <LinkedInIcon className={classes.icon} />
              </a>
            </Grid>
            <Grid item>
              <a href="https://github.com/henry32144">
                <GitHubIcon style={{ fontSize: "36px" }} className={classes.icon} />
              </a>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </div >
  );
}

Banner.propTypes = {
}

Banner.defaultProps = {
}

export default Banner